<template>
<div id='patientInfo'>
    <el-form :model="ruleForm" ref="ruleForm" label-width="100px"  style="margin: 0; padding: 0;">
    <el-card>
      <h4></h4>
      <el-form-item label=":" prop="patname" required style="width: 33%; display: inline-block">
        <el-input v-model="ruleForm.name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label=":" prop="patregion" style="width: 33%; display: inline-block">
        <el-select v-model="ruleForm.patregion" placeholder="" style="width: 49%; display: inline-block">
          <el-option label="" value="shanghai"></el-option>
          <el-option label="" value="shenzhen"></el-option>
          <el-option label="" value="guangzhou"></el-option>
          <el-option label="" value="beijing"></el-option>
        </el-select>
        <el-input v-model="ruleForm.patregionjt" style="width: 49%; display: inline-block;"></el-input>
      </el-form-item>
      <el-form-item label=":" prop="patidentity" required style="width: 33%; display: inline-block;">
        <el-input v-model="ruleForm.identity" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label=":" prop="patsex" required style="width: 33%; display: inline-block;">
        <el-radio-group v-model="ruleForm.patsex">
          <el-radio label=""></el-radio>
          <el-radio label=""></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label=":" required style="width: 33%; display: inline-block;">
          <el-form-item prop="patdate1">
            <el-date-picker type="date" placeholder="" v-model="ruleForm.patdate1" style="width: 100%;"></el-date-picker>
          </el-form-item>
      </el-form-item>
      <el-form-item label=":" prop="pattel" required style="width: 33%; display: inline-block;">
        <el-input v-model="ruleForm.pattel" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label=":" style="width: 100%; display: block;">
        <el-select v-model="ruleForm.adrp" placeholder="" style="width: 14%; display: inline-block;">
        <el-option label="" value="gaungdong"></el-option>
        <el-option label="" value="hainan"></el-option>
        <el-option label="" value="jiangxi"></el-option>
        <el-option label="" value="hebei"></el-option>
        <el-option label="" value="hunan"></el-option>
        <el-option label="" value="qinghai"></el-option>
      </el-select>
      <el-select v-model="ruleForm.adrs" placeholder="" style="width: 14%; display: inline-block;">
        <el-option label="" value="gaungzhou"></el-option>
        <el-option label="" value="jieyang"></el-option>
        <el-option label="" value="chaozhou"></el-option>
        <el-option label="" value="zhanjiang"></el-option>
        <el-option label="" value="heyuan"></el-option>
        <el-option label="" value="huizhou"></el-option>
      </el-select>
      <el-input v-model="ruleForm.adrjt" placeholder="" style="width: 71%; display: inline-block;"></el-input>
      </el-form-item>
      <h4></h4>
      <el-form-item label=":" style="width: 33%; display: inline-block;">
        <el-input placeholder=""></el-input>
      </el-form-item>
      <el-form-item label=":" style="width: 33%; display: inline-block;">
        <el-input placeholder=""></el-input>
      </el-form-item>
      <el-form-item label=":" style="width: 33%; display: inline-block;">
        <el-input placeholder=""></el-input>
      </el-form-item>
      <el-form-item prop="patAH" label=":" style="width: 39.5%; display: block;">
        <el-input v-model="patientDetailsAH.patAH"></el-input>
      </el-form-item>
        <el-form-item v-for="(domain, index) in patientDetailsAH.domains" :key="domain.key" :prop="'domains.' + index + '.value'">
        <el-input v-model="domain.value" style="width: 33.3%; display: inline-block;"></el-input>
        <el-button @click.prevent="removeDomain(domain)" style="border: none; color: #0086CD; display: inline-block;">
          <i class="el-icon-delete2"></i>
        </el-button>
        </el-form-item>
        <el-button @click="addDomain()" style="border: none; margin-left: 88px; color: #0086CD; display: block;">
        <i class="el-icon-plus"></i>
        <span></span>
        </el-button>
        <el-form-item label=":">
          <el-upload :on-preview="handlePreview" :on-remove="handleRemove" :file-list="fileList" style="margin-top: 0px;">
            <el-button class="patinfo-uploadbtn" style="margin-left: 0; padding-left: 0px;"></el-button>
          </el-upload>
        </el-form-item>
    </el-card>
      <el-button type="success" class="patinfo-submitbtn"></el-button>
    </el-form>
</div>
  
</template>

<script>
  export default {
    data() {
      return {
        patientDetailsAH: {
          domains: [{
            value: ''
          }],
          patAH: '',
        },
        ruleForm: {
          patname: '',
          patregion: '',
          patregionjt: '',
          patdate1: '',
          patsex: '',
          pattel: '',
          adrp: '',
          adrs: '',
          adrjt: '',
          linkername: '',
          linkertel: '',
          link: '',
          patAH: ''
        },
        rules: {
          patname: [
            { message: '', trigger: 'blur' },
            { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
          ],
          // patregion: [
          //   { message: '', trigger: 'change' }
          // ],
          patregionjt: [
            { message: '', trigger: 'change' }
          ],
          patdate1: [
            { type: 'date', message: '', trigger: 'change' }
          ],
          patsex: [
            { message: '', trigger: 'change' }
          ],
          adrp: [
            { message: '', trigger: 'change' }
          ],
          adrs: [
            { message: '', trigger: 'change' }
          ],
          adrjt: [
            { message: '', trigger: 'change' }
          ],
          patsex: [
            { message: '', trigger: 'change' }
          ],
          pattel: [
            { message: '', trigger: 'blur' }
          ],
          linkername: [
            { message: '', trigger: 'blur' }
          ],
          linkertel: [
            { message: '', trigger: 'blur' }
          ],
          link: [
            { message: '', trigger: 'blur' }
          ],
          patAH: [
	     	{ trigger: 'blur', trigger: 'blur,change' }
       	  ],
       	  domain: [
	      	{ message: '', trigger: 'blur'}
	   	  ]
        },
        fileList: [{name: 'food.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}, {name: 'food2.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}]
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$router.push({path: '/patientInfo'});
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      removeDomain(item) {
        var index = this.patientDetailsAH.domains.indexOf(item)
        if (index !== -1) {
          this.patientDetailsAH.domains.splice(index, 1)
        }
      },
      addDomain() {
        this.patientDetailsAH.domains.push({
          value: '',
          key: Date.now()
        });
      }
    }
  }
</script>

<style>
#patientInfo .patinfo-uploadbtn{
	border: none;
	color: #0086CD;
	margin-left: -15px;
}
#patientInfo .patinfo-submitbtn{
	float: right;
	margin-top: 10px;
}
</style>


