import Vue from 'vue';
import { api } from '@/global/api.js'//
const item={
	 // 
	state: {
		projectTab:'1',
		infoItems:[],
		selectItems:[],
		classify:[],
	},
	mutations: {
		getData(state){
			let k= state;
			//this.$httpvue
			Vue.http.get(api.item).then(function(response){
				k.infoItems=response.data.infoItems;
				k.classify=response.data.classify;
			}, function(response){
				this.$message('.');
			})
		},
	},
};
export default item;


